import React from 'react';

function Schema({ title, description, url, category, categoryUrl, img }) {
    return (
        <>
            <script type="application/ld+json">
                {`[{
        "@context": "http://schema.org",
        "@type": "BlogPosting",
        "headline": ${title},
        "description": ${description},
        "mainEntityOfPage":{
            "@type":"WebPage",
            "@id": ${url}
        },
        "image": ${img},
        "author": {
            "@type": "Person",
            "name":  "Ian Chu"
        },
        "publisher": {
            "@type": "Organization",
            "name":  "Ian Chu",
            "logo": {
              "@type": "ImageObject",
              "url":  "https://iamian.cc/favicon.png",
              "width":  600 ,
              "height":  60 
            }
        }
      },
      {
        "@type": "BreadcrumbList",
        "itemListElement": [{
          "@type": "ListItem",
          "position": 1,
          "name": ${category},
          "item": ${categoryUrl}
        },{
          "@type": "ListItem",
          "position": 2,
          "name": ${title},
          "item": ${url}
        }]
      }]`}
            </script>
        </>
    );
}

export default Schema;
